@import url(https://fonts.googleapis.com/css?family=Heebo:300&display=swap);
 {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.fullScreen {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(84, 139, 175, 0.1);
}

.highlight {
  height: 170px;
  color: rgba(34, 53, 116);
  background-color: rgba(255, 255, 255, 0.1);
  transition-duration: 2000ms;
}

.highlight:hover {
  background-color: rgba(255, 255, 255, 0.5);
  transition-duration: 2000ms;

}
.btnChat {
    display: flex;
    margin-top: 90px;
    float: right;
}

.imsSize {
    width: 400px;
    height: 400px;
}
.styleButtonInfo {

    border-radius: 35;
    background-color: "#21b6ae";
    padding: "18px 36px";
    font-size: "18px";

}
.btnLogin {
    display: flex;
    float: right;
}

.box {
    height: 100;
    display: flex;
    border: 1px ;
    padding: 8;
}

.topLeftBox {
    justify-content: center;
    align-items: flex-end;
}
.bot {
    bottom: 0;
    right: 0;
    margin: 10px;
    position: fixed;
    width: 350px;
    z-index: 94;
}

.btn {
    margin: 5px;
    width: 40px;
    height: 40px;
    bottom: 40px;
    right: 40px;
    margin-bottom: 30px;
    float: right;
    background-color: #21b6ae;
    color: #fff;
    border-radius: 50px;
    text-align: center;
    box-shadow: 2px 2px 3px #999;
}

.btn:hover {
    opacity: 0.9;
}

.btn:focus {
    outline: none;
}

a {
    text-decoration: none;
}

.img {
    height: 200px;
    width: 300px;
}

